/*********************
    - 2. General -
*********************/

@charset "UTF-8";

html {
  overflow-y: scroll;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

/* ボディ
---------------------------------------------------------------------------- */

body {
  background-color: #FFFFFF;
  color: #131313;
  text-align: left;
  font-size: 15px;
  // font-size: 90%;
  font-family: "Open Sans";
  line-height: 1.6em;
}
.text-center{
  text-align: center;
}
h1, h2, h3, h4, h5, h6, address,p {
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-family: "Open Sans";
  margin: 0;
  padding: 0;
  line-height: 20px;
}

textarea {
  font-size: 1em;
  font-weight: normal;
  font-style: normal;
  font-family: "Open Sans";
  margin: 0;
  padding: 0;
  line-height: 1.6em;
}

img {
  border: none;
  vertical-align: bottom;
}

hr {
  display: none;
}

li {
  list-style-type: none;
}

::selection, ::-moz-selection {
  background-color: #FEE4EB;
}

/* clearfix */
/*--------------------------------------------------------*/
.active{
  border-bottom: none;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

* {
  html .clearfix, &:first-child + html .clearfix {
    zoom: 1;
  }
}

/* IE6用ハック */
/* IE7用ハック */

.clear {
  clear: both;
  hr {
    display: none;
  }
}

