/* table */
/*--------------------------------------------------------*/

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0px;
}

/* リンク
---------------------------------------------------------------------------- */

a {
  overflow: hidden;
  outline: none;
  &:hover, &:active {
    text-decoration: none;
    color: #6c1044;
  }
  overflow-x: hidden;
  overflow-y: hidden;
}

/*	common
----------------------------------------------------*/

.fade {
  -webkit-transition: 0.2s ease-in-out;
  -moz-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  &:hover {
    /*standard opacity set*/
    -ms-filter: "alpha( opacity=70 )";
    /* for IE 8 & 9 (filter works too) should come BEFORE "filter:" */
    filter: alpha(opacity = 70);
    /*IE 9 8 7 6 5 */
    -moz-opacity: 0.7;
    /* Older than Firefox 0.9 */
    -khtml-opacity: 0.7;
    /* Safari 1.x (pre WebKit!) */
    opacity: 0.7;
    /* Firefox 0.9+, Safari 2?, Chrome Opera 9+, IE 9+ */
  }
}

.fade2 {
  /* no opacity*/
  -webkit-transition: 0.2s ease-in-out;
  -moz-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}
button{
  outline: 0;
  cursor: pointer;
}
/*fade for mobile*/

.fade3 {
  -webkit-transition: 0.2s ease-in-out;
  -moz-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  &:active {
    /*standard opacity set*/
    -ms-filter: "alpha( opacity=70 )";
    /* for IE 8 & 9 (filter works too) should come BEFORE "filter:" */
    filter: alpha(opacity = 70);
    /*IE 9 8 7 6 5 */
    -moz-opacity: 0.7;
    /* Older than Firefox 0.9 */
    -khtml-opacity: 0.7;
    /* Safari 1.x (pre WebKit!) */
    opacity: 0.7;
    /* Firefox 0.9+, Safari 2?, Chrome Opera 9+, IE 9+ */
  }
}

.absmid {
  /* css version of absmiddle, image vertical style alignment */
  vertical-align: middle;
  margin-bottom: .25em;
}

